@import url(https://fonts.googleapis.com/css?family=Muli:regular,bold,light&subset=latin,latin-ext);
body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Muli', sans-serif;
}

code {
  font-family: 'Muli', sans-serif;
}

.App {
  text-align: center;
  overflow-x: hidden;
}

.awardList {
    text-align: left;
    width:33%;

}

.list-content {
    position: relative;
    left:3vw;
    font-size: 22px;
    color: #3B9BC2

}

.list-name {
    color: #3B9BC2;
    position: relative;

    left: 4.3vw;
    display: inline-block;
    background: -webkit-linear-gradient(white 50%,#D5EBF6)

}

.list-element {
    display: flex;
    flex-direction: row;
}

.input-field {
    position: relative;
    top: 0%;
    text-align:center;
    width:100%;
}


.list-input {
    height: 40px;
    width: 70%;
    font-size: 20px;
    border: none;
    border-bottom: 2px solid #8AC9E1;
    border-top: 2px solid #8AC9E1;
}

::-webkit-input-placeholder {
    color: #8AC9E1;



}

::-ms-input-placeholder {
    color: #8AC9E1;



}

::placeholder {
    color: #8AC9E1;



}

:focus {
    outline: none;
}

.close-button {
  width: 90%;
  position: relative;
  top: 0%;
  right: 20%;
  height: 30px;
  width: 30px;
  cursor: pointer;

}


.ListItem {
  width: 90%;
}

.categoryInputPage {
  height: 100vh; /* Magic here */
  width: 100vw;

  justify-content: center;
  align-items: center;
  text-align: center;

  background-color: #d5ebf6;
}

.title {
  font-weight: bold;
  position: relative;
  color: #3b9bc2;
  font-size: 50px;
  text-align: left;

  left:10%;
  top: 1%;
}

.category_body {
  position: relative;
  z-index: 1;
  top: 5%;
  margin-left: auto;
  margin-right: auto;

  width: 90vw;
  height: 80vh;
  background-color: white;
}

.category_lists {
  width:inherit;
  height:inherit;
  display: flex;
  flex-direction: row;
}

.next_button {
  font-weight: bold;
  position: absolute;
  background: #d5ebf6;
  color: #3b9bc2;
  font-size: 30px;
  border-radius: 4px;
  bottom: 50px;
  width: 110px;
  height: 50px;
  box-shadow: 3px 3px #cdcdcd;
}

.view {
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
  background-color: #d5ebf6;
  color: #3b9bc2;
  font-family: 'Muli', sans-serif;
}

.pheader {
  font-weight: 600;
  color: #3b9bc2;
  text-align: left;
  margin-left: 7vw;
}

.box {
  font-weight: 600;
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  height: 75%;
  background-color: white;
  padding: 25px;
  overflow-y: auto;
}

.c {
  width: 40vw;
  display: flex;
  align-items: center;
  margin-left: 20vw;
  justify-content: space-between;
}

.datatype {
  margin: 10px;
}

.input-box {
  width: 25%;
  padding: 12px 10px;
  margin: 5px 0;
  border: none;
  border-bottom: 2.5px solid #d5ebf6;
  border-top: 2.5px solid #d5ebf6;
}

.input-box::-webkit-input-placeholder {
  color: #d5ebf6;
  font-size: 10px;
  font-weight: 500;
}

.input-box::-ms-input-placeholder {
  color: #d5ebf6;
  font-size: 10px;
  font-weight: 500;
}

.input-box::placeholder {
  color: #d5ebf6;
  font-size: 10px;
  font-weight: 500;
}

.button {
  background-color: #d5ebf6;
  margin: 10px;
  font-family: 'Muli', sans-serif;
  font-size: 15px;
  font-weight: 1000;
  color: #3b9bc2;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  border-radius: 10px;
  box-shadow: 2.5px 2.5px 5px grey;
}

.upload-button {
  padding: 10% 20px;
  margin-top: 0px;
  margin: auto;
}

.upload-file {
  width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
  position: absolute;
	z-index: -1;
}

.upload-file + label {
  background-color: #d5ebf6;
  margin: 10px;
  font-family: 'Muli', sans-serif;
  font-size: 15px;
  font-weight: 1000;
  color: #3b9bc2;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  border-radius: 10px;
  box-shadow: 2.5px 2.5px 5px grey;
  padding: 15px 25px;
}

.submit-button-box {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  top: 5%;
}

.submit-button {
  font-size: 20px;
  padding: 15px 25px;
}

html,
body,
div,
p {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

@font-face {
  font-family: 'Muli', sans-serif;
}

.judge-info-page {
  background: #d5ebf6;
  min-height: 100vh;
}

.judge-info-title {
  font-family: 'Muli';
  font-weight: bold;
  color: #3b9bc2;
  text-align: left;
  font-size: 4.5vh;
  margin-bottom: 2vh;
  padding-top: 10vh;
  padding-left: 18vh;
}

.input-list {
  background: white;
  display: block;
  margin-left: auto;
  margin-right: auto;
  align-content: center;
  justify-content: center;
  width: 84%;
  height: 82vh;
}

.input-name-header {
  font-family: 'Muli';
  font-weight: bold;
  color: #3b9bc2;
  font-size: 3.5vh;
  padding-top: 4vh;
  text-align: left;
}

:focus {
  outline: none;
}

::-webkit-input-placeholder {
  color: #98d0ec;
  font-size: 2vh;
}

::-ms-input-placeholder {
  color: #98d0ec;
  font-size: 2vh;
}

::placeholder {
  color: #98d0ec;
  font-size: 2vh;
}

.input-name {
  border: none;
  width: 100%;
  display: inline-block;
  align-content: center;
  justify-content: center;
  color: #98d0ec;
  margin-top: 2vh;
  border-top: 2px solid #98d0ec;
  border-bottom: 2px solid #98d0ec;
  height: 5.25%;
  font-family: 'Muli';
  font-weight: regular;
  font-size: 2vh;
  padding-left: 2%;
}

.input-api-header {
  font-family: 'Muli';
  font-weight: bold;
  color: #3b9bc2;
  font-size: 3.5vh;
  padding-top: 4vh;
  text-align: left;
  margin-bottom: 2vh;
}

.submit-button-judge {
  font-family: 'Muli';
  font-weight: bold;
  font-size: 2.5vh;
  color: #3b9bc2;
  display: block;
  align-content: center;
  justify-content: center;
  margin-top: 3vh;
  border: none;
  background-color: #d5ebf6;
  width: 14vh;
  height: 6vh;
  box-shadow: 0 0px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 3px 0 rgba(0, 0, 0, 0.19);
}

.submit-button-judge:hover {
  background-color: #c9e0ec;
  box-shadow: 0 0px 0px 0 rgba(0, 0, 0, 0.24), 0 4px 4px 0 rgba(0, 0, 0, 0.19);
}

.judge-name-list {
  height: 50%;
  text-align: center;
  border-radius: 50px;
  background: #d5ebf6;
}


.judge-info-list-border {
  padding: 4%;
  position: relative;
  display: inline-block;
  align-content: center;
  overflow: auto;
  width: 65%;
}

.name-list-header {
  display: inline-block;
  font-family: 'Muli';
  font-weight: bold;
  color: #3b9bc2;
  background-color: #98d0ec;
  font-size: 3.5vh;
  text-align: left;
  padding: 1%;
  padding-left: 5%;
  width: 44%;
}

.api-list-header {
  display: inline-block;
  font-family: 'Muli';
  font-weight: bold;
  color: #3b9bc2;
  background-color: #98d0ec;
  font-size: 3.5vh;
  text-align: left;
  padding: 1%;
  padding-left: 5%;
  width: 44%;
}

.dropdown {
  display: block;
  width: 100%;
}

.input-container {
  width: 35%;
  display: inline-block;
  margin-left: 5vh;
}

.delete {
  display: inline-block;
  width: 19px;
  height: 18px;
  text-align: center;
  background-color: #D2D2D2;
  border-radius: 50%;
  font-size: 12px;
  line-height: 18px;
  cursor: pointer;
}

ul {
  padding:0;
  margin:0;
}

.judge-item {
  display: flex;
  flex-direction: row;
  border-bottom: 2px solid #d5ebf6;
}

.remove-button {
  border: none;
  height: 50%;
}

.button-icon {
  color: #d5ebf6;
  font-size: 4vh;
}

.delete-button {
  border: none;
  justify-content: center;
  color: #d5ebf6;
  font-size: 3vh;
}

.delete-button:hover{
  color: #91bcd1;
}

.judge-button {
  background-color: #d5ebf6;
  margin: 10px;
  font-family: 'Muli', sans-serif;
  font-size: 20px;
  font-weight: 1000;
  color: #3b9bc2;
  padding: 15px 30px;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  border-radius: 10px;
  box-shadow: 2.5px 2.5px 5px grey;
}

.buttons {
  position: relative;
  margin-left: 40%;
}

.entry-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  height:80%;
}

.list {
  overflow:scroll; 
  height:80%;
}
.dark-color {
  background-color: #d5ebf6;
}

.light-color {
  background-color: white;
}

.judge {
  width: 100%;
}

.judge-api {
  position: relative;
  display: inline-block;
  font-family: 'Muli';
  font-weight: bold;
  color: #3b9bc2;
  font-size: 2.5vh;
  text-align: left;
  width: 45%;
  padding: 0.5%;
  padding-left: 7%;
}

.judge-name {
  position: relative;
  display: inline-block;
  font-family: 'Muli';
  font-weight: bold;
  color: #3b9bc2;
  font-size: 2.5vh;
  text-align: left;
  width: 45%;
  padding: 0.5%;
}
